import React from 'react';
import {Card, CardActions, CardHeader, CardText} from 'material-ui/Card';
import RaisedButton from 'material-ui/RaisedButton';
import Graph from './graph'

export default class TickerCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      expanded: false, loaded:false, playing:false, data: []
    };
    
    this.data = []
    this.prodURL = "https://hidden-island-42423.herokuapp.com/api/"+props.ticker+"&x="+props.x+"&p=15&i="+props.inter+"&p=5d&f=o"
    this.devURL = "http://localhost:3003/api/"+props.ticker+"&x="+props.x+"&p=15&i="+props.inter+"&p=5d&f=o"

    fetch(this.prodURL, {
        method: "GET",
        mode: 'cors',
        headers:{
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Credentials':true,
            'Access-Control-Allow-Methods': 'GET'
            }
    }).then(response => { return response.text() }).then( response=>{
        let lines = response.split('\n')
        lines.splice(0, 8)
        lines.forEach(line => {
            this.data.push(line)
        })


        this.generateNotes(this.data)
        this.setState({loaded:true})
    })  
  }
    generateNotes(data) {
        data.pop()
        this.open = data.splice(0,1)
        this.notes = []

        data.forEach(val => {
            this.notes.push(Math.floor(((val-this.open) / this.open) * 1000))
        });
    }

  addData(i){
    this.setState({data: [...this.state.data, this.data[i]]})
  }

  handleExpandChange = (expanded) => {
    this.setState({expanded: expanded});
  };

  clearData(){
    this.setState({data: []})
  }

  handleExpand = () => {
    this.setState({expanded: true});
  };

  handleReduce = () => {
    this.setState({expanded: false});
  };

  play(){
    this.props.play(this.notes, this.props.ticker)
    this.setState({expanded: true})
  }

  render() {
    var on_show_styles = {maxHeight: "2000px", transition: "max-height 4s ease-in"};
    var on_hide_styles = {maxHeight: "0", transition: "max-height 0.15s ease-out", overflow: "hidden"};
    return (
    <Card 
      expanded={this.state.expanded}
      onExpandChange={this.handleExpandChange}
    >
        <CardHeader
            title={this.props.ticker}
            subtitle={this.props.x}
            actAsExpander={true}
            showExpandableButton={true}
        />
        <CardActions>
          <RaisedButton label="Play" onClick={this.play.bind(this)} />
          <RaisedButton label="Stop" onClick={() =>this.props.stop(this.props.ticker)}/>
        </CardActions>
        <CardText expandable={true} >
          <Graph style={this.state.expanded ? on_show_styles : on_hide_styles} className="card-graph" data={this.state.data}></Graph>
      </CardText>
    </Card>
    );
  }
}

  



