import React, {Component} from 'react';
import AutoComplete from 'material-ui/AutoComplete';
import RaisedButton from 'material-ui/RaisedButton/RaisedButton';
import DropDownMenu from 'material-ui/DropDownMenu';
import MenuItem from 'material-ui/MenuItem';


const exchanges = [
    "NASDAQ",
    "NYSE",
    "AMEX"
]

export default class AutoCompleteExampleSimple extends Component {
  state = {
    dataSource: [],
    exchange: "NASDAQ",
    value: 0
  };

  componentDidMount(){
    fetch("tickers.json").then((response) => {
    return response.json() }).then((response) =>{
        this.tickers = response
        this.setState({dataSource: response["NASDAQ"]})
    })
  }

  handleNewRequest = (value) => {
      this.props.addTicker(value, this.state.exchange)
  }

  handleChange = (event, index, value) => { 
      this.setState({value, exchange: exchanges[value], dataSource: this.tickers[exchanges[value]]}) 
    
    }
    ;

  render() {
    return (
      <div className="searchbar-wrapper">
        <AutoComplete
          className="searchbar"
          hintText="Search ticker"
          filter={AutoComplete.fuzzyFilter}
          dataSource={this.state.dataSource}
          onUpdateInput={this.handleUpdateInput}
          maxSearchResults={5}
          onNewRequest={this.handleNewRequest}
        />

        <DropDownMenu className="searchbar-dropdown" value={this.state.value} onChange={this.handleChange}>
          <MenuItem value={0} primaryText="NASDAQ" />
          <MenuItem value={1} primaryText="NYSE" />
          <MenuItem value={2} primaryText="AMEX" />
        </DropDownMenu>
      </div>
    );
  }
}



