import React, { Component } from 'react';
import './App.css';

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import TickerCard from "./tickercard"
import NotePlayer from './music'
import Header from "./header"
import AutoCompleteExampleSimple from "./searchbar"

let featuredStocks = [
  {
      name: "Apple",
      ticker: "AAPL",
      exchange: "NASDAQ"
  },
  {
      name: "Micron",
      ticker: "MU",
      exchange: "NASDAQ"
  },
  {
      name: "AMD",
      ticker: "AMD",
      exchange: "NASDAQ"
  },
  {
    name: "S&P 500",
    ticker: "SPY",
    exchange: "NYSEARCA"
  },
]

class App extends Component {
  constructor(props){
    super(props)

    this.dataAdded = (i) => this.tickerClasses[this.currentTicker].addData(i)
    this.soundPlayer = new NotePlayer(this.dataAdded)
    this.state = {tickers: featuredStocks, sample: "pianos"}
    this.tickerClasses = {}
  }

  addTicker = (ticker, exchange) => {
    this.setState({tickers:  [{name: ticker, ticker, exchange}, ...this.state.tickers]})
  }

  play(buffer, ticker){
    this.currentTicker = ticker
    this.tickerClasses[ticker].clearData()
    this.soundPlayer.play(buffer)
  }
  stop(ticker){
    this.soundPlayer.stop()
    this.tickerClasses[ticker].clearData()
  }

  render() {
    return (
      <MuiThemeProvider>
        <div>
          <Header setSample={(sample) => this.soundPlayer.sample = sample}></Header>

          <div className="center-column-wrapper">
            <AutoCompleteExampleSimple addTicker={this.addTicker}></AutoCompleteExampleSimple>
            <div className="tickerlist">
              {this.state.tickers.map((stock)=> 
                <TickerCard
                  ref={(input) => { this.tickerClasses[stock.ticker] = input }} 
                  key={stock.ticker} 
                  ticker={stock.ticker} 
                  x={stock.exchange} 
                  play={this.play.bind(this)} 
                  stop={this.stop.bind(this)}
                  inter="300"></TickerCard>
              )}
              </div>

            </div>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default App;




